.icons{
    position: fixed;
    bottom: 30px;
    width: 50px;
    display: flex;
    flex-direction: column;
    margin: 5px;
    padding:10px;
}

body{
    background-image: url('../images/calgary.png');
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.marg{
    transform: translateY(-25%);
}

.construct{
    text-align: center;
    margin-top: 5vh;
}

#uc{
    font-size: 90px;
    margin-bottom: 30px;
}

.sub{
    font-size: 35px;
    font-style: italic;
    margin: 15px;
}

.first{
    margin-top: 100px;
}

.second{
    margin-bottom: 90px;
}

.construct div:progress{
    display: table-cell;
    text-align: center;
}

.progress { 
    height: 17px;
    width: 30%;
    margin: 0 auto;
    background: rgb(12, 12, 12);
    -moz-border-radius: 25px;
    -webkit-border-radius: 25px;
    border-radius: 25px;
    padding: 10px;
    box-shadow: inset 0 -1px 1px rgba(255,255,255,0.3);
}

#bar {
    /* display: block; */
    height: 100%;
    width: 30%;
    /* this is where I control the % of progress bar */
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    background-color: white;
    box-shadow: 
        inset 0 2px 9px  rgba(255,255,255,0.3),
        inset 0 -2px 6px rgba(0,0,0,0.4);
    text-align: center;
    position: relative;
    overflow: hidden;
}